import { ActivatedRoute } from '@angular/router';
import { District } from '../models/district';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-district-wise-details',
  templateUrl: './district-wise-details.component.html',
  styleUrls: ['./district-wise-details.component.css']
})
export class DistrictWiseDetailsComponent implements OnInit {

  totalActive = 0;
  totalDeceased = 0;
  totalConfirmed = 0;
  totalRecovered = 0;

  stateData: any;
  stateName: string;
  stateDataObject: any;
  district: District;
  districts: District[] = [];
  constructor(private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.stateName = this.route.snapshot.paramMap.get('statename');
    this.stateDataObject = this.route.snapshot.paramMap.get('statedata');
    this.stateData = this.stateDataObject;
    // console.log(JSON.parse(this.stateData));
    this.showDistrictWiseData(JSON.parse(this.stateData));
  }

  showDistrictWiseData(districtWiseData) {
    const temparr = [];
    let tempDistricts: District[] = [];
    let tempDistrict = {
      name: '',
      active: 0,
      deceased: 0,
      confirmed: 0,
      recovered: 0
    }
    Object.keys(districtWiseData).map(function (key1) {
      tempDistrict = {
        name: '',
        active: 0,
        deceased: 0,
        confirmed: 0,
        recovered: 0
      }
      tempDistrict.name = key1;
      tempDistrict.active = districtWiseData[key1].active;
      tempDistrict.deceased = districtWiseData[key1].deceased;
      tempDistrict.confirmed = districtWiseData[key1].confirmed;
      tempDistrict.recovered = districtWiseData[key1].recovered;

      tempDistricts.push(tempDistrict);
      // console.log(key1);
      // console.log(districtWiseData[key1].confirmed);
      temparr.push({ key1: districtWiseData[key1] });
      return temparr;
    });
    this.districts = tempDistricts;
    this.getTotalCount(tempDistricts);
    console.log(temparr);
  }

  getTotalCount(tempDistricts) {
    tempDistricts.forEach(element => {
      this.totalActive += element.active;
      this.totalDeceased += element.deceased;
      this.totalRecovered += element.recovered;
      this.totalConfirmed += element.confirmed;
    });
  }
}
