import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class WorldCoivd19CasesService {

  constructor(private httpClient: HttpClient) { }

  getAllCountriesCovid19Cases() {
    return this.httpClient.get('https://corona.lmao.ninja/v2/countries?yesterday&sort');
  }

}
