import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class Covid19IndiaService {

  constructor(private httpClient: HttpClient) { }

  getCovid19Count() {
    console.warn('Caling COVID service');
    return this.httpClient.get('https://api.covid19india.org/state_district_wise.json');
  }

  getHistoricalData(){
    return this.httpClient.get('https://corona.lmao.ninja/v2/historical/india?lastdays=100');
  }
}
