import { Covid19IndiaService } from './../shared/covid19-india.service';
import { MovieService } from './../shared/movie.service';
import { TopicService } from './../shared/topic.service';
import { Topic } from './../models/topic';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-apppage',
  templateUrl: './apppage.component.html',
  styleUrls: ['./apppage.component.css']
})
export class ApppageComponent implements OnInit {

  errorMsg: string;
  username: string;
  topics: Topic[] = [];
  constructor(private route: ActivatedRoute, private router: Router, private topicService: TopicService, private covid19IndiaService: Covid19IndiaService) { }

  ngOnInit(): void {
    this.username = this.route.snapshot.paramMap.get('username');
    this.getAllTopics();
  }

  getAllTopics() {
    this.topicService.getAllTopics()
      .subscribe(
        data => {
          this.topics = data;
        },
        error => {
          this.errorMsg = error;
        }
      );
  }

  searchTopic(topic) {
    this.router.navigate(['/moviesHomePage']);
  }
}
