import { WorldCoivd19CasesService } from './../world-coivd19-cases.service';
import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-world-coivd19-cases',
  templateUrl: './world-coivd19-cases.component.html',
  styleUrls: ['./world-coivd19-cases.component.css']
})
export class WorldCoivd19CasesComponent implements OnInit {

  dtOptions: DataTables.Settings = {};
  dtTrigger: any = new Subject();

  constructor(private worldCoivd19CasesService: WorldCoivd19CasesService) { }

  results: any;
  worldCountryData: []= [];
  totalCases: any = 0;
  totalTests: any = 0;
  totalActiveCases: any = 0;
  totalDeaths: any = 0;
  totalRecovered: any = 0;
  totalTodaysDeaths: any = 0;
  totalTodaysCases: any = 0;

  currentDate = new Date();
  ngOnInit(): void {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 200
    };
    this.getAllCountriesCovid19Cases();
  }

  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }
  getAllCountriesCovid19Cases() {
    this.worldCoivd19CasesService.getAllCountriesCovid19Cases()
      .subscribe(
        data => {
          console.log(data);
          this.results = data;
          this.worldCountryData = this.results;
          this.dtTrigger.next();
          this.results.forEach(element => {
            this.totalCases += element.cases;
            this.totalTests += element.tests;
            this.totalActiveCases += element.active;
            this.totalDeaths += element.deaths;
            this.totalRecovered += element.recovered;
            this.totalTodaysCases += element.todayCases;
            this.totalTodaysDeaths += element.todayDeaths;
          });
        }, error => {
          console.log(error);
        }
      );
  }
}
