import { Covid19IndiaService } from './../shared/covid19-india.service';
import { Label, MultiDataSet, SingleDataSet, Color } from 'ng2-charts';
import { Component, OnInit } from '@angular/core';
import { ChartType } from 'chart.js';

@Component({
  selector: 'app-doughnut-chart',
  templateUrl: './doughnut-chart.component.html',
  styleUrls: ['./doughnut-chart.component.css']
})
export class DoughnutChartComponent implements OnInit {

  constructor(private covid19IndiaService: Covid19IndiaService) { }

  results: any;
  doughnutChartLabels: Label[] = ['Deaths', 'Cases', 'Recovered'];
  doughnutChartData: MultiDataSet = [
    
  ];
  doughnutChartType: ChartType = 'doughnut';

  doughnutChartColor: Color[] = [
    { backgroundColor: 'green' },
    { backgroundColor: 'red' },
    { backgroundColor: 'blue' }
];
  ngOnInit(): void {
    this.getHistoricalData();
  }

  getHistoricalData() {
    this.covid19IndiaService.getHistoricalData()
      .subscribe(
        data => {
          this.results = data;
          const caseDateArrayData = [];
          const caseCountArrayData = [];
          const recoveredCountArrayData = [];
          const deathCountArrayData = [];

          const casesObject = this.results.timeline.cases;
          const deceasedObject = this.results.timeline.deaths;
          const recoveredObject = this.results.timeline.recovered;

          //this.barChartData[0].label = this.results.country + ' COVID19 Cases';
          Object.keys(casesObject).map(function (key) {
            caseDateArrayData.push(key);
            caseCountArrayData.push(casesObject[key]);
          });

          Object.keys(recoveredObject).map(function (key) {
            recoveredCountArrayData.push(recoveredObject[key]);
          });

          Object.keys(deceasedObject).map(function (key) {
            deathCountArrayData.push(deceasedObject[key]);
          });

          this.doughnutChartData[0] = deathCountArrayData[deathCountArrayData.length - 1];
          this.doughnutChartData[1] = caseCountArrayData[caseCountArrayData.length - 1];
          this.doughnutChartData[2] = recoveredCountArrayData[recoveredCountArrayData.length - 1];
        }, error => {
          console.error(error);
        }
      );
  }
}
