import { Covid19IndiaService } from './../shared/covid19-india.service';
import { Component, OnInit } from '@angular/core';
import { ChartDataSets, ChartOptions } from 'chart.js';
import { Color, Label } from 'ng2-charts';

@Component({
  selector: 'app-line-chart',
  templateUrl: './line-chart.component.html',
  styleUrls: ['./line-chart.component.css']
})
export class LineChartComponent implements OnInit {

  constructor(private covid19IndiaService: Covid19IndiaService) { }

  results: any;
  lineChartData1: ChartDataSets[] = [
    { data: [], label: '', backgroundColor: 'grey', hoverBackgroundColor: 'grey' },
  ];

  lineChartData2: ChartDataSets[] = [
    { data: [], label: '', backgroundColor: 'green', hoverBackgroundColor: 'grey' }
  ];

  lineChartData3: ChartDataSets[] = [
    { data: [], label: '', backgroundColor: 'red', hoverBackgroundColor: 'grey' }
  ];
  lineChartLabels: Label[] = [];
  lineChartOptions = {
    responsive: true,
  };

  lineChartColors: Color[] = [
    {
      borderColor: 'black',
      backgroundColor: 'rgba(255,255,0,0.28)',
    },
  ];

  lineChartLegend = true;
  lineChartPlugins = [];
  lineChartType = 'line';

  ngOnInit(): void {
    this.getHistoricalData();
  }

  getHistoricalData() {
    this.covid19IndiaService.getHistoricalData()
      .subscribe(
        data => {
          this.results = data;
          const caseDateArrayData = [];
          const caseCountArrayData = [];
          const recoveredCountArrayData = [];
          const deathCountArrayData = [];

          const casesObject = this.results.timeline.cases;
          const deceasedObject = this.results.timeline.deaths;
          const recoveredObject = this.results.timeline.recovered;

          this.lineChartData1[0].label = this.results.country + ' COVID19 Cases';
          this.lineChartData2[0].label = this.results.country + ' COVID19 Recovered';
          this.lineChartData3[0].label = this.results.country + ' COVID19 Deaths';
          Object.keys(casesObject).map(function (key) {
            caseDateArrayData.push(key);
            caseCountArrayData.push(casesObject[key]);
          });

          Object.keys(recoveredObject).map(function (key) {
            recoveredCountArrayData.push(recoveredObject[key]);
          });

          Object.keys(deceasedObject).map(function (key) {
            deathCountArrayData.push(deceasedObject[key]);
          });
          
          this.lineChartData1[0].data = caseCountArrayData;
          this.lineChartData2[0].data = recoveredCountArrayData;
          this.lineChartData3[0].data = deathCountArrayData;
          this.lineChartLabels = caseDateArrayData;
        }, error => {
          console.error(error);
        }
      );
  }


}
