import { Covid19IndiaService } from './../shared/covid19-india.service';
import { Label } from 'ng2-charts';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-bar-chart',
  templateUrl: './bar-chart.component.html',
  styleUrls: ['./bar-chart.component.css']
})
export class BarChartComponent implements OnInit {

  //spring.cloud.config.uri: http://localhost:8888
//   app:
//     name: Config App
//     description: This is my app name ${app.name}     
      
// my:
//    greeting: Hello
//    list: one,two,three
   

  results: any;
  constructor(private covid19IndiaService: Covid19IndiaService) { }

  barChartOptions: ChartOptions = {
    responsive: true,
  };
  barChartLabels: Label[] = [];
  barChartType: ChartType = 'bar';
  horizontalBarC: ChartType = 'horizontalBar';
  barChartLegend = true;
  barChartPlugins = [];

  barChartData1: ChartDataSets[] = [
    { data: [], label: '', backgroundColor: 'grey', hoverBackgroundColor: 'black' }
  ];
  barChartData2: ChartDataSets[] = [
    { data: [], label: '', backgroundColor: 'green', hoverBackgroundColor: 'black' }
  ];

  barChartData3: ChartDataSets[] = [
    { data: [], label: '', backgroundColor: 'red', hoverBackgroundColor: 'black' }
  ];

  barChartCompleteData: ChartDataSets[] = [
    { data: [], label: 'COVID19 Cases Death', backgroundColor: 'green', hoverBackgroundColor: 'grey' },
    { data: [], label: 'COVID19 Cases Recovered', backgroundColor: 'red', hoverBackgroundColor: 'grey' }
  ];

  ngOnInit(): void {
    this.getHistoricalData();
  }

  getHistoricalData() {
    this.covid19IndiaService.getHistoricalData()
      .subscribe(
        data => {
          this.results = data;
          const caseDateArrayData = [];
          const caseCountArrayData = [];
          const recoveredCountArrayData = [];
          const deathCountArrayData = [];

          const casesObject = this.results.timeline.cases;
          const deceasedObject = this.results.timeline.deaths;
          const recoveredObject = this.results.timeline.recovered;

          this.barChartData1[0].label = this.results.country + ' COVID19 Cases';
          this.barChartData2[0].label = this.results.country + ' COVID19 Recovered';
          this.barChartData3[0].label = this.results.country + ' COVID19 Deaths';

          Object.keys(casesObject).map(function (key) {
            caseDateArrayData.push(key);
            caseCountArrayData.push(casesObject[key]);
          });

          Object.keys(recoveredObject).map(function (key) {
            recoveredCountArrayData.push(recoveredObject[key]);
          });

          Object.keys(deceasedObject).map(function (key) {
            deathCountArrayData.push(deceasedObject[key]);
          });

          this.barChartData1[0].data = caseCountArrayData;
          this.barChartData2[0].data = recoveredCountArrayData;
          this.barChartData3[0].data = deathCountArrayData;

          this.barChartCompleteData[0].data = deathCountArrayData;
          this.barChartCompleteData[1].data = recoveredCountArrayData;

          this.barChartLabels = caseDateArrayData;
        }, error => {
          console.error(error);
        }
      );
  }

}
