import { Router } from '@angular/router';
import { State } from './../models/state';
import { Covid19IndiaService } from './../shared/covid19-india.service';
import { Component, OnInit } from '@angular/core';
import { Movie } from '../models/movie';

@Component({
  selector: 'app-covid19india',
  templateUrl: './covid19india.component.html',
  styleUrls: ['./covid19india.component.css']
})
export class Covid19indiaComponent implements OnInit {

  results: any;
  state: State;
  movie: Movie;
  states: State[] = [];

  showBar = true;
  showLine = false;
  totalActive = 0;
  totalDeceased = 0;
  totalConfirmed = 0;
  totalRecovered = 0;
  constructor(private covid19IndiaService: Covid19IndiaService, private router: Router) { }

  ngOnInit(): void {
    this.getCovid19Count();
  }

  toggleCharts(selectedChart) {
    if (selectedChart === 'barchart') {
      this.showBar = true;
      this.showLine = false;
    } else {
      this.showBar = false;
      this.showLine = true;
    }
  }
  getCovid19Count() {
    // console.log('Caling COVID controller');
    this.covid19IndiaService.getCovid19Count()
      .subscribe(
        data => {
          const myArray = [];
          const arr = [];
          const temparr = [];
          let tempStates: State[] = [];
          let tempState = {
            name: '',
            stateCode: '',
            active: 0,
            deceased: 0,
            confirmed: 0,
            recovered: 0
          }
          let temp1 = [];
          let temp2 = [];
          let active = 0;
          let deceased = 0;
          let confirmed = 0;
          let recovered = 0;
          Object.keys(data).map(function (key) {
            tempState = {
              name: '',
              stateCode: '',
              active: 0,
              deceased: 0,
              confirmed: 0,
              recovered: 0
            };
            active = 0;
            deceased = 0;
            confirmed = 0;
            recovered = 0;
            // console.log(key);
            tempState.name = key;
            temp1 = data[key];
            Object.keys(temp1).map(function (key1) {
              if (!temp1[key1].length) {
                tempState.stateCode = temp1[key1];
              }
              temp2 = temp1[key1];
              Object.keys(temp2).map(function (key2) {
                // console.log(key2);
                // console.log(temp2[key2].confirmed);
                if (temp2[key2].confirmed) {
                  confirmed += temp2[key2].confirmed;
                }
                if (temp2[key2].recovered) {
                  recovered += temp2[key2].recovered;
                }
                if (temp2[key2].deceased) {
                  deceased += temp2[key2].deceased;
                }
                if (temp2[key2].recovered) {
                  recovered += temp2[key2].recovered;
                }
                if (temp2[key2].active) {
                  active += temp2[key2].active;
                }
                temparr.push({ [key2]: temp2[key2] });
                return temparr;
              });
              arr.push({ [key1]: temp1[key1] });
              return arr;
            });
            tempState.active = active;
            tempState.deceased = deceased;
            tempState.confirmed = confirmed;
            tempState.recovered = recovered;
            tempStates.push(tempState);
            // console.log('Confirmed in ' + key + ' is: ' + confirmed);
            myArray.push({ [key]: data[key] });
            return myArray;
          });
          // console.log(myArray);
          // console.log(arr);
          this.states = tempStates;
          this.getTotalCount(tempStates);
        }, error => {
          // console.error(error);
        }
      );
  }

  getTotalCount(tempStates) {
    //alert('called');
    tempStates.forEach(element => {
      this.totalActive += element.active;
      this.totalDeceased += element.deceased;
      this.totalRecovered += element.recovered;
      this.totalConfirmed += element.confirmed;
    });
  }

  moreDetails(stateName, stateData) {
    // console.log(stateName);
    console.log(stateData);
    this.router.navigate(['/districtWiseDetails', { statename: stateName, statedata: JSON.stringify(stateData) }]);
  }

}