import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class MovieService {

  API_KEY = '863d5b4fa9a0181fcfabd33f79d3d271';
  constructor(private httpClient: HttpClient) { }

  getTopRatedMovies() {
    return this.httpClient.get(`https://api.themoviedb.org/3/movie/top_rated?api_key=${this.API_KEY}&language=en-US&page=1`);
  }

  getMyRatedMoviesInfo(){
    return this.httpClient.get('');
  }
}

